.release {
  h2 {
    font-size: 26px;
    font-weight: 800;
    margin: 0;
    display: flex;
    align-items: center;
    gap: 8px;

    .badge {
      background-color: #007aff;
      color: #fff;
      padding: 3px 6px;
      border-radius: 4px;
      font-size: 14px;
      font-weight: 600;
    }
  }

  .info {
    color: #666;
    font-size: 14px;

    .download {
      margin-left: 6px;
    }
  }
}